body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}
.answer {
  border-radius: 6px;
  color: #ffffff;
  font-size: 16px;
  background: #9e9e9e;
  outline: none;
  padding: 10px 20px 10px 20px;
  text-decoration: none;
  margin: 3px;
}

.answer:hover {
  background: #a3a3a3;
  background-image: -webkit-linear-gradient(top, #a3a3a3, #737373);
  background-image: -webkit-gradient(linear, left top, left bottom, from(#a3a3a3), to(#737373));
  background-image: linear-gradient(to bottom, #a3a3a3, #737373);
}
.trivia-container {
    border: 1px solid black;
    font-size: 2em;
    margin: 20px;
    margin-bottom: 0;
    padding: 10px;
    padding-bottom: 0;
    border-radius: 20px;
    max-width: 1000px;
}

.difficulty {
    margin-bottom: 10px;
    font-size: 0.7em;
}

.question {
    font-family: 'Saira Semi Condensed', sans-serif;
    font-weight: 500;
}

.answers-list {
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
}

.score-col {
    display: flex;
    flex-direction: row;
    justify-content: center;
    bottom: 0;
}

.score-col>div {
    padding: 10px;
    border: 1px solid black;
}

.game-info {
    display: grid;
    grid-template-rows: repeat(2, 1fr);
    justify-content: center;
    margin-top: 20px;
    font-size: 0.6em;
}

.answered {
    background-color: #c0ff7d;
}

.remaining {
    background-color: #c2fffc;
}

.unanswered {
    background-color: #ff9cad;
}

.game-info>h4 {
    border: 1px solid black;
    border-top-left-radius: 20px;
    border-top-right-radius: 20px;
}


.game-score {
    display: flex;
    justify-content: center;
    align-items: center;
}

.game-score>p {
    padding: 5px;
}

.total-score {
    border-style: solid;
    border-color: black;
    border-width: 1px 1px 1px 1px;
    border-radius: 0 0 10px 10px;
    padding: 5px;
    font-size: 1.7rem;
}

@media only screen and (max-width: 800px) {
    .trivia-container {
        font-size: 1.5em;
    }

    .answers-list {
        flex-direction: column;
    }
}
.main-menu-container {
	display: flex;
	justify-content: center;
	align-items: center;
	flex-direction: column;
}

.title {
	margin-bottom: 20px;
	font-weight: 700;
	font-size: 3em;
}

.rules-info {
	margin-top: 13px;
}

.button {
	box-shadow: 0px 1px 0px 0px #fff6af;
	background: -webkit-gradient(linear, left top, left bottom, color-stop(0.05, #ffec64), color-stop(1, #ffab23));
	background: -webkit-linear-gradient(top, #ffec64 5%, #ffab23 100%);
	background: -webkit-gradient(linear, left top, left bottom, color-stop(5%, #ffec64), to(#ffab23));
	background: linear-gradient(to bottom, #ffec64 5%, #ffab23 100%);
	filter: progid:DXImageTransform.Microsoft.gradient(startColorstr='#ffec64', endColorstr='#ffab23', GradientType=0);
	background-color: #ffec64;
	border-radius: 8px;
	display: inline-block;
	cursor: pointer;
	color: #3d3d3d;
	font-size: 15px;
	font-weight: bold;
	padding: 5px 21px;
	text-decoration: none;
	text-shadow: 0px 1px 0px #ffe730;
	outline: none;
}

.button:hover {
	background: -webkit-gradient(linear, left top, left bottom, color-stop(0.05, #ffab23), color-stop(1, #ffec64));
	background: -webkit-linear-gradient(top, #ffab23 5%, #ffec64 100%);
	background: -webkit-gradient(linear, left top, left bottom, color-stop(5%, #ffab23), to(#ffec64));
	background: linear-gradient(to bottom, #ffab23 5%, #ffec64 100%);
	filter: progid:DXImageTransform.Microsoft.gradient(startColorstr='#ffab23', endColorstr='#ffec64', GradientType=0);
	background-color: #ffab23;
}

.button:active {
	position: relative;
	top: 1px;
}

.categories-container {
	display: flex;
	justify-content: center;
	align-items: center;
	flex-direction: column;
	border: 1px solid black;
	padding: 13px;
	margin-top: 90px;
}

.categories-container h1 {
	font-weight: 700;
	font-size: 1.5em;
	margin-bottom: 10px;
}

.categories-container p {
	font-weight: 600;
	margin-bottom: 5px;

}

.category {
	border: 1.2px solid black;
	border-radius: 20px;
	width: 280px;
	margin-bottom: 10px;
}

.category-container {
	margin: 10px;
	border-bottom: 1px solid black;
}

.category-title {
	font-size: 1.5em;
	margin-top: 5px;
	margin-bottom: 10px;
	font-weight: 700;
}

.category-name {
	font-family: 'Saira Semi Condensed', sans-serif;
	font-weight: 600;
	margin-bottom: 10px;
}

.category-button {
	border-radius: 6px;
	color: #ffffff;
	font-size: 16px;
	background: #9e9e9e;
	outline: none;
	padding: 10px 20px 10px 20px;
	text-decoration: none;
	margin: 3px;
}

.coins-container {
	padding: 10px;
	display: flex;
	justify-content: center;
	align-items: center;
	flex-direction: row;

}

.shop {
	margin-left: 50px;
}

.settings {
	border: 1px solid black;
	border-top-left-radius: 30%;
	border-top-right-radius: 30%;
	border-bottom: none;
	margin-bottom: 0px;
	padding: 20px;
}

.odometer.odometer-auto-theme, .odometer.odometer-theme-default {
	display: inline-block;
	vertical-align: middle;
	*vertical-align: auto;
	*zoom: 1;
	*display: inline;
	position: relative;
  }
  .odometer.odometer-auto-theme .odometer-digit, .odometer.odometer-theme-default .odometer-digit {
	display: inline-block;
	vertical-align: middle;
	*vertical-align: auto;
	*zoom: 1;
	*display: inline;
	position: relative;
  }
  .odometer.odometer-auto-theme .odometer-digit .odometer-digit-spacer, .odometer.odometer-theme-default .odometer-digit .odometer-digit-spacer {
	display: inline-block;
	vertical-align: middle;
	*vertical-align: auto;
	*zoom: 1;
	*display: inline;
	visibility: hidden;
  }
  .odometer.odometer-auto-theme .odometer-digit .odometer-digit-inner, .odometer.odometer-theme-default .odometer-digit .odometer-digit-inner {
	text-align: left;
	display: block;
	position: absolute;
	top: 0;
	left: 0;
	right: 0;
	bottom: 0;
	overflow: hidden;
  }
  .odometer.odometer-auto-theme .odometer-digit .odometer-ribbon, .odometer.odometer-theme-default .odometer-digit .odometer-ribbon {
	display: block;
  }
  .odometer.odometer-auto-theme .odometer-digit .odometer-ribbon-inner, .odometer.odometer-theme-default .odometer-digit .odometer-ribbon-inner {
	display: block;
	-webkit-backface-visibility: hidden;
  }
  .odometer.odometer-auto-theme .odometer-digit .odometer-value, .odometer.odometer-theme-default .odometer-digit .odometer-value {
	display: block;
	-webkit-transform: translateZ(0);
  }
  .odometer.odometer-auto-theme .odometer-digit .odometer-value.odometer-last-value, .odometer.odometer-theme-default .odometer-digit .odometer-value.odometer-last-value {
	position: absolute;
  }
  .odometer.odometer-auto-theme.odometer-animating-up .odometer-ribbon-inner, .odometer.odometer-theme-default.odometer-animating-up .odometer-ribbon-inner {
	-webkit-transition: -webkit-transform 2s;
	transition: -webkit-transform 2s;
	transition: transform 2s;
	transition: transform 2s, -webkit-transform 2s;
  }
  .odometer.odometer-auto-theme.odometer-animating-up.odometer-animating .odometer-ribbon-inner, .odometer.odometer-theme-default.odometer-animating-up.odometer-animating .odometer-ribbon-inner {
	-webkit-transform: translateY(-100%);
	transform: translateY(-100%);
  }
  .odometer.odometer-auto-theme.odometer-animating-down .odometer-ribbon-inner, .odometer.odometer-theme-default.odometer-animating-down .odometer-ribbon-inner {
	-webkit-transform: translateY(-100%);
	transform: translateY(-100%);
  }
  .odometer.odometer-auto-theme.odometer-animating-down.odometer-animating .odometer-ribbon-inner, .odometer.odometer-theme-default.odometer-animating-down.odometer-animating .odometer-ribbon-inner {
	-webkit-transition: -webkit-transform 2s;
	transition: -webkit-transform 2s;
	transition: transform 2s;
	transition: transform 2s, -webkit-transform 2s;
	-webkit-transform: translateY(0);
	transform: translateY(0);
  }
  
  .odometer.odometer-auto-theme, .odometer.odometer-theme-default {
	font-family: "Helvetica Neue", sans-serif;
	line-height: 1.1em;
  }
  .odometer.odometer-auto-theme .odometer-value, .odometer.odometer-theme-default .odometer-value {
	text-align: center;
  }
